/* Header Component Styles */
.header {
  background: var(--primary);
  box-shadow: var(--shadow-lg);
  position: fixed;
  width: 100%;
  top: 0;
  z-index: var(--z-fixed);
  transition: all var(--transition-normal);
}

.header.scrolled {
  background: rgba(20, 22, 25, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: var(--shadow-2xl);
}

.header .container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--space-6);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-4) 0;
  position: relative;
  min-height: 80px;
}

/* Logo Styles */
.logo {
  flex-shrink: 0;
}

.logo a {
  text-decoration: none;
  transition: all var(--transition-fast);
}

.logo h1 {
  color: var(--white);
  font-size: var(--text-2xl);
  font-weight: 800;
  margin: 0;
  background: linear-gradient(135deg, var(--white), var(--accent-light));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  transition: all var(--transition-fast);
}

.logo a:hover h1 {
  transform: scale(1.05);
}

/* Navigation Styles */
.navbar {
  display: flex;
  align-items: center;
}

.nav-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

.nav-item {
  position: relative;
  margin: 0 var(--space-2);
}

.nav-link {
  color: var(--white);
  text-decoration: none;
  font-weight: 600;
  font-size: var(--text-base);
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  background: none;
  border: none;
  cursor: pointer;
  font-family: inherit;
  position: relative;
  overflow: hidden;
  min-height: 48px;
  white-space: nowrap;
}

.nav-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(233, 69, 96, 0.2), transparent);
  transition: left var(--transition-slow);
}

.nav-link:hover::before {
  left: 100%;
}

.nav-link:hover,
.nav-link:focus {
  background: rgba(233, 69, 96, 0.1);
  color: var(--accent-light);
  transform: translateY(-2px);
  outline: none;
}

/* Hamburger Menu */
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 32px;
  height: 32px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
}

.hamburger span {
  width: 32px;
  height: 3px;
  background: var(--white);
  border-radius: var(--radius-full);
  transition: all var(--transition-normal);
  transform-origin: 1px;
}

.hamburger.active span:first-child {
  transform: rotate(45deg);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
  transform: translateX(20px);
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg);
}

/* Dropdown Styles */
.dropdown {
  position: relative;
}

.dropdown-toggle::after {
  content: '▼';
  font-size: var(--text-xs);
  margin-left: var(--space-2);
  transition: transform var(--transition-fast);
}

.dropdown.active .dropdown-toggle::after {
  transform: rotate(180deg);
}

/* Enhanced Dropdown Menu */
.dropdown-menu {
  position: absolute;
  top: calc(100% + 8px);
  background: var(--white);
  width: min(85vw, 800px);
  max-width: calc(100vw - 40px);
  box-shadow: var(--shadow-2xl);
  border-radius: var(--radius-2xl);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  transition: all var(--transition-normal);
  z-index: 1001;
  border: 1px solid var(--border-color);
  overflow: hidden;
  margin-top: var(--space-2);
  will-change: transform, opacity;
}

/* Desktop positioning */
@media (min-width: 769px) {
  .dropdown-menu {
    right: var(--dropdown-offset, 0);
    left: auto;
  }
  
  .dropdown-menu.left {
    left: var(--dropdown-offset, 0);
    right: auto;
  }
  
  .dropdown:hover .dropdown-menu,
  .dropdown.active .dropdown-menu {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}

/* Mobile positioning */
@media (max-width: 768px) {
  .dropdown-menu {
    position: static;
    opacity: 1;
    visibility: visible;
    transform: none;
    box-shadow: none;
    background: var(--primary-light);
    border-radius: 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height var(--transition-normal) ease-in-out;
    width: 100%;
    border: none;
    margin: 0;
  }

  .dropdown.active .dropdown-menu {
    max-height: 70vh;
    overflow-y: auto;
  }
}

.products-dropdown {
  display: grid;
  grid-template-columns: minmax(200px, 280px) 1fr;
  min-height: min(450px, 75vh);
  max-height: 80vh;
  overflow: hidden;
}

.dropdown-categories {
  background: var(--bg-secondary);
  border-right: 1px solid var(--border-color);
  padding: var(--space-6) 0;
  overflow-y: auto;
}

.category-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: var(--space-4) var(--space-6);
  background: none;
  border: none;
  text-align: left;
  font-size: var(--text-sm);
  font-weight: 500;
  color: var(--text-primary);
  cursor: pointer;
  transition: all var(--transition-fast);
  border-left: 3px solid transparent;
  min-height: 52px;
  font-family: inherit;
}

.category-item:hover,
.category-item.active {
  background: var(--primary);
  color: var(--white);
  border-left-color: var(--accent);
}

.dropdown-products {
  padding: var(--space-6);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.products-header {
  margin-bottom: var(--space-6);
  text-align: center;
  border-bottom: 2px solid var(--accent);
  padding-bottom: var(--space-4);
  width: 100%;
  max-width: 500px;
}

.products-header h3 {
  font-size: var(--text-xl);
  color: var(--white);
  margin: 0;
  font-weight: 700;
}

.products-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--space-3);
  align-content: start;
  max-width: 500px;
  width: 100%;
  justify-items: stretch;
}

.product-link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-3) var(--space-4);
  color: var(--text-primary);
  text-decoration: none;
  font-size: var(--text-sm);
  font-weight: 500;
  border-radius: var(--radius-md);
  transition: all var(--transition-fast);
  border: 1px solid var(--border-color);
  min-height: 48px;
  text-align: center;
  background: var(--white);
}

.product-link:hover,
.product-link:focus {
  background: var(--accent);
  color: var(--white);
  transform: translateY(-2px);
  border-color: var(--accent);
  box-shadow: var(--shadow-md);
  outline: none;
}

/* Responsive Design */

/* Large Desktop */
@media (min-width: 1400px) {
  .dropdown-menu {
    width: min(80vw, 850px);
  }
  
  .products-dropdown {
    grid-template-columns: 300px 1fr;
  }
}

/* Medium Desktop */
@media (max-width: 1199px) and (min-width: 992px) {
  .dropdown-menu {
    width: min(70vw, 650px);
  }
  
  .products-dropdown {
    grid-template-columns: 250px 1fr;
  }
}

/* Tablet */
@media (max-width: 991px) and (min-width: 769px) {
  .dropdown-menu {
    width: min(65vw, 580px);
  }
  
  .products-dropdown {
    grid-template-columns: 220px 1fr;
    min-height: min(400px, 70vh);
  }
  
  .products-list {
    grid-template-columns: 1fr;
    max-width: 350px;
  }
  
  .category-item {
    padding: var(--space-3) var(--space-4);
    font-size: var(--text-xs);
    min-height: 48px;
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .header-content {
    padding: var(--space-3) 0;
    min-height: 70px;
  }
  
  .logo h1 {
    font-size: var(--text-xl);
  }
  
  .hamburger {
    display: flex;
  }
  
  .nav-menu {
    position: fixed;
    left: -100vw;
    top: 70px;
    flex-direction: column;
    background: var(--primary);
    width: 100vw;
    text-align: left;
    transition: left var(--transition-normal);
    box-shadow: var(--shadow-2xl);
    height: calc(100vh - 70px);
    overflow-y: auto;
    padding: 0;
    align-items: stretch;
    z-index: 9999;
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-item {
    margin: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    width: 100%;
  }

  .nav-item:last-child {
    border-bottom: none;
  }

  .nav-link {
    padding: var(--space-4) var(--space-6);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0;
    width: 100%;
    min-height: 56px;
    text-align: left;
  }

  .nav-link:hover::before {
    display: none;
  }

  .products-dropdown {
    display: flex;
    flex-direction: column;
    min-height: auto;
    max-height: none;
    width: 100%;
    grid-template-columns: none;
  }

  .dropdown-categories {
    background: var(--primary-dark);
    border-right: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 0;
    max-height: 25vh;
    overflow-y: auto;
    width: 100%;
    flex-shrink: 0;
  }

  .category-item {
    padding: var(--space-3) var(--space-6);
    color: rgba(255, 255, 255, 0.9);
    min-height: 48px;
    border-left: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    width: 100%;
    text-align: left;
    font-size: var(--text-sm);
  }

  .category-item:last-child {
    border-bottom: none;
  }

  .category-item:hover,
  .category-item.active {
    background: var(--accent);
    color: var(--white);
  }

  .dropdown-products {
    padding: var(--space-4);
    max-height: 45vh;
    background: var(--primary-light);
    width: 100%;
    overflow-y: auto;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .products-header {
    text-align: left;
    border-bottom: 1px solid rgba(233, 69, 96, 0.3);
    margin-bottom: var(--space-4);
    padding-bottom: var(--space-3);
  }

  .products-header h3 {
    color: var(--accent-light);
    font-size: var(--text-lg);
    margin: 0;
  }

  .products-list {
    display: flex;
    flex-direction: column;
    gap: 0;
    width: 100%;
    grid-template-columns: none;
  }

  .product-link {
    color: rgba(255, 255, 255, 0.8);
    padding: var(--space-3) var(--space-4);
    font-size: var(--text-sm);
    min-height: 44px;
    border-radius: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    width: 100%;
    text-align: left;
    display: flex;
    align-items: center;
    border: none;
    background: transparent;
  }

  .product-link:last-child {
    border-bottom: none;
  }

  .product-link:hover,
  .product-link:focus {
    background: var(--accent);
    color: var(--white);
    transform: none;
  }
}

/* Small Mobile */
@media (max-width: 576px) {
  .header .container {
    padding: 0 var(--space-4);
  }
  
  .logo h1 {
    font-size: var(--text-lg);
  }
  
  .hamburger {
    width: 28px;
    height: 28px;
  }
  
  .hamburger span {
    width: 28px;
    height: 2px;
  }
  
  .nav-link {
    padding: var(--space-3) var(--space-4);
    font-size: var(--text-sm);
    min-height: 52px;
  }
  
  .dropdown-categories {
    max-height: 150px;
  }
  
  .category-item {
    padding: var(--space-2) var(--space-4);
    font-size: var(--text-sm);
    min-height: 44px;
  }
  
  .dropdown-products {
    padding: var(--space-3);
    max-height: 250px;
  }
  
  .products-header h3 {
    font-size: var(--text-base);
  }
  
  .product-link {
    padding: var(--space-2) var(--space-3);
    font-size: var(--text-sm);
    min-height: 40px;
  }
}

/* Performance Optimizations */
@media (prefers-reduced-motion: reduce) {
  .header,
  .nav-link,
  .dropdown-menu,
  .hamburger span,
  .category-item,
  .product-link {
    transition: none;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .header {
    border-bottom: 2px solid var(--primary);
  }
  
  .nav-link {
    border: 1px solid transparent;
  }
  
  .nav-link:hover,
  .nav-link:focus {
    border-color: var(--primary);
  }
  
  .dropdown-menu {
    border: 2px solid var(--primary);
  }
}

/* Print styles */
@media print {
  .header {
    position: static;
    box-shadow: none;
  }
  
  .dropdown-menu {
    display: none;
  }
}
